.TodoItem{
    background-color: #1f1c1c;
    border-radius: 30px;
    position: relative;
    color: rgba(251, 252, 252, 0.904);
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 18px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}

.TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.TodoItem-p--complete {
    text-decoration: line-through;
}

.Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
}

.Icon-check {
    position: absolute;
    left: 12px;
    color: #434443;
}

.Icon-check:hover{
    color: #0bbb19;
}

.Icon-check--activate {
    color: #0bbb19;
}

.Icon-delete {
    position: absolute;
    top: -24px;
    font-weight: bold;
    color: #5c655d;
    right: 0%;
}
.Icon-delete:hover {
    color: rgb(186, 7, 10);
}